<template>
  <div>
    <header-nav v-bind:class="{'isShare':active}"></header-nav>
    <div class="mineInfo_box">
    		<h3></h3>
    		<p>
    			<span><i>***</i></span>
    			<span class="switchBtn" v-on:click="switchType" v-bind:class="{'switchHide':isHide}"><b></b></span>
    		</p>
    		<p>
    			<span><i></i></span>
    		</p>
    		<p>
    			<span><i>123456</i></span>
    		</p>
    </div>
    	<!---->
    	<pop v-bind:class="{'isPop':isShowPop}"></pop>
  </div>
</template>

<script>
	import HeaderNav from '../../components/header.vue'
	import Pop from '../../components/pop.vue'
	
	export default {
	  data () {
	    return {
	      active:true,
	      isHide:true,
	      isShowPop:true
	    }
	  },
	  components:{
	  	HeaderNav,
	  	Pop
	  },
	  methods:{
	  	switchType(){
	  		this.isHide = !this.isHide;
	  		if(this.isHide==false){
	  			this.isShowPop = false;
	  		}
	  	}
	  }
	}
</script>
<style scoped>
	.mineInfo_box{
		margin-top: 50px;
		padding: 30px;
	}
	h3{
		font-size: 24px;
		line-height: 50px;
	}
	p{
		text-indent: 2em;
		line-height: 32px;
	}
	.switchBtn{
		display: inline-block;
		vertical-align: middle;
		width: 80px;
		height: 32px;
		border-radius: 15px;
		border: 1px solid #e5e5e5;
		background: #41B883;
		position: relative;
		margin-left: 30px;
	}
	.switchBtn b{
		position: absolute;
		width: 28px;
		height: 28px;
		border-radius:50%;
		background: #fff;
		left: 3px;
		top: 1px;
	}
	.switchHide{
		background: #f5f5f5;
	}
	.switchHide b{
		background: #42B983;
		left: auto;
		right: 3px;
	}
	.isPop{
		display: none;
	}
</style>



