<template>
  <div>
    <header-nav v-bind:class="{'isShare':active}" class="dateInfo_head"></header-nav>
    <div class="main">
    	<ul>
    		<li v-for="items in articles">
    			<router-link v-bind:to="items.linkUrl">{{items.name}}</router-link>
    		</li>
    	</ul>
    </div>
    <router-view></router-view>
    <div class="footer">~Q: 643764957</div>
  </div>
</template>

<script>
	import HeaderNav from '../../components/header.vue'

	export default {
	  data () {
	    return {
	      active:true,
	      articles:[{
	      	name:'vue-cli+webpackSPA',
	      	linkUrl:"/share/vue-cliAndWebpack"
	      }]
	    }
	  },
	  components:{
	  	HeaderNav
	  },
	  methods:{
	  }
	}
</script>
<style scoped>
	.footer{
		text-align: center;
		color: #D3D3D3;
	}
</style>



