import Vue from 'vue'
import Router from 'vue-router'
import Index from '../page/index.vue'
import MineInfo from '../page/share/mineInfo.vue'
import DateInfo from '../page/share/dateInfo.vue'
import VuecliAndWebpack from '../page/share/vue-cliAndWebpack.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      meta:{
      	title:"L-pf"
      },
      beforeEnter(to,from,next){
      	console.log(to)
      	if(to.meta.title){
      		document.title = to.meta.title
      	}else{
      		document.title = "-"
      	}
      	next();
      }
    },
    {
    	path: '/share/mineInfo',
      name: 'MineInfo',
      component: MineInfo,
      meta:{
      	title:"L-pf"
      },
      beforeEnter(to,from,next){
      	if(to.meta.title){
      		document.title = to.meta.title
      	}else{
      		document.title = "L-pf"
      	}
      	next();
      }
    },
    {
    	path: '/share/dateInfo',
      name: 'DateInfo',
      component: DateInfo,
      meta:{
      	title:"L-pf"
      },
      beforeEnter(to,from,next){
      	console.log(to)
      	if(to.meta.title){
      		document.title = to.meta.title
      	}else{
      		document.title = "L-pf"
      	}
      	next();
      },
      children:[{
  		path: '/share/vue-cliAndWebpack',
	    name: 'VuecliAndWebpack',
	    component: VuecliAndWebpack,
	    meta:{
	      	title:"vue-cli+webpackSPA"
	      },
	      beforeEnter(to,from,next){
	      	console.log(to)
	      	if(to.meta.title){
	      		document.title = to.meta.title
	      	}else{
	      		document.title = "L-pf"
	      	}
	      	next();
	      }
  	}]
    },
    
    	
  ]
})



