<template>
	<div class="share">
		<ul>
			<li v-for="items in shareList">
				<router-link v-bind:to="items.linkUrl">{{items.name}}</router-link>
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		name:"share",
		data(){
			return {
				shareList:[{
					name:"",
					linkUrl:"/share/mineInfo"
				},{
					name:"",
					linkUrl:"/share/dateInfo"
				}]
			}
		}
	}
</script>

<style scoped>
	ul{
		width: 100px;
		background: #fff;
	}
	ul li{
		width:100%;
		height: 30px;
		line-height: 30px;
		text-align: center;
		
		box-sizing: border-box;
	}
	ul li a:hover{
		background: #42B983;
		color: #fff;
		border-color: #42B983;
	}
	ul li a{
		display: block;
		color: #333;
		border-bottom: 1px solid #ddd;
		border-left: 1px solid #ddd;
		border-right: 1px solid #ddd;
	}
	.dateInfo_head .share ul li:nth-child(2) a{
		background: #42B983;
		color: #fff;
		border-color: #42B983;
	}
</style>


