			<p class="popTitle"></p>
			<p class="popContent"></p>
		</div>
	</div>
</template>

<script>
	export default{
		methods:{
			closePop(){
				
			}
		}
	}
</script>

<style scoped>
	.pop{
		width: 250px;
		min-height: 150px;
		background: #fff;
		border: 1px solid #e5e5e5;
		border-radius: 5px;
		top: 35%;
		left: 50%;
		margin-left: -125px;
	}
</style>


